<style lang="scss" scoped>
    @import "./css/style.scss";
</style>
<template>
    <div id="sendOrder">
        <el-card>
            <el-form class="u-edit-from" :model="ruleForm" :rules="rules" ref="ruleForm" label-position="left" label-width="160px">
                <div class="multiple-title-card">
                    <div class="title">基本信息</div>
                    <el-form-item label="服务用户：" prop="userType">
                        <el-select v-model="ruleForm.region" placeholder="请选择服务用户">
                            <el-option
                                v-for="(v,k) in $Cfg.postStatus"
                                :key="k"
                                :label="v"
                                :value="k">
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <Patient/>

                    <el-form-item label="联系姓名：" prop="userName">
                        <el-input v-model="ruleForm.name" placeholder="请输入联系人姓名"></el-input>
                    </el-form-item>
                    <el-form-item label="联系人电话：" prop="phone">
                        <el-input v-model="ruleForm.phone" placeholder="请输入联系人电话"></el-input>
                    </el-form-item>
                    <el-form-item label="服务地址：" prop="userType">
                        <div class="server-address">
                            <CityCascader v-model="cityList" size="middle" style="width: 220px;"></CityCascader>
                            <el-input v-model="ruleForm.detailAddress" placeholder="请输入具体地址"
                                      style="width: 300px; marginLeft: 20px"></el-input>
                        </div>
                    </el-form-item>
                </div>
                <div class="multiple-title-card">
                    <div class="title">服务产品信息</div>
                    <el-form-item label="是否选用平台套餐：" prop="userType">
                        <el-select v-model="ruleForm.region" placeholder="请选择">
                            <el-option
                                v-for="(v,k) in $Cfg.postStatus"
                                :key="k"
                                :label="v"
                                :value="k">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="服务产品：" prop="userName">
                        <el-input v-model="ruleForm.name" placeholder="请输入服务产品"></el-input>
                    </el-form-item>
                    <el-form-item label="计时方式：" prop="userName">
                        <el-radio-group v-model="timeUnit">
                            <el-radio :label="3">分</el-radio>
                            <el-radio :label="6">小时</el-radio>
                            <el-radio :label="9">天</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="服务时长：" prop="userName">
                        <el-input v-model="ruleForm.name" placeholder="请输入服务小时"></el-input>
                    </el-form-item>
                    <el-form-item label="服务时间：" prop="userName">
                        <el-date-picker
                            v-model="ruleForm.time"
                            type="datetime"
                            placeholder="选择日期时间">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="服务年资：" prop="userType">
                        <el-select v-model="ruleForm.region" placeholder="请选择护士服务年资">
                            <el-option
                                v-for="(v,k) in $Cfg.postStatus"
                                :key="k"
                                :label="v"
                                :value="k">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="技能标签：" prop="userType">
                        <el-radio-group v-model="ruleForm.skillText">
                            <el-radio :label="3">心脏病护士</el-radio>
                            <el-radio :label="6">打针护士</el-radio>
                            <el-radio :label="9">心脏病护士</el-radio>
                            <el-radio :label="1">打针护士</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="服务金额：" prop="userName">
                        <el-input v-model="ruleForm.name" placeholder="请输入服务金额"></el-input>
                    </el-form-item>
                    <el-form-item label="购买数量：" prop="userType">
                        <el-select v-model="ruleForm.region" placeholder="选择招募人数">
                            <el-option
                                v-for="(v,k) in $Cfg.postStatus"
                                :key="k"
                                :label="v"
                                :value="k">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="总服务金额：" prop="userName">
                    </el-form-item>
                    <el-form-item label="其他服务备注：" prop="userType">
                        <el-input
                            type="textarea"
                            :autosize="{ minRows: 4, maxRows: 6}"
                            placeholder="请输入内容"
                            style="width: 610px"
                            v-model="ruleForm.remarks">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="接单范围：" prop="userType">
                        <el-select v-model="ruleForm.region" placeholder="选择接单范围">
                            <el-option
                                v-for="(v,k) in $Cfg.postStatus"
                                :key="k"
                                :label="v"
                                :value="k">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </div>
                <el-button type="primary" @click="payOrder">去支付</el-button>
            </el-form>
        </el-card>
    </div>
</template>

<script>
    import CityCascader from '@/components/CityCascader/CityCascader'
    import Patient from './Patient'
    import PayDialog from '../common/PayDialog'

    export default {
        data() {
            return {
                cityList: [],
                ruleForm: {},
                rules: {},
                timeUnit: '',
                checkedCities: [],
                cities: ['上海', '北京', '广州', '深圳'],
                numbers: []
            }
        },
        methods: {
            payOrder(row) {
                const h = this.$createElement;
                this.$msgbox({
                    title: '支付方式',
                    message: h('PayDialog', {props: {pay_money: 3000}},),
                    showCancelButton: false,
                    showConfirmButton: false,
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    beforeClose: (action, instance, done) => {
                        if (action === 'confirm') {
                            instance.confirmButtonLoading = true;
                            instance.confirmButtonText = '执行中...';
                            setTimeout(() => {
                                done();
                                setTimeout(() => {
                                    instance.confirmButtonLoading = false;
                                }, 300);
                            }, 3000);
                        } else {
                            done();
                        }
                    }
                }).then(action => {
                    this.$message({
                        type: 'info',
                        message: 'action: ' + action
                    });
                });
            },
        },
        created() {
            for (let i = 1; i <= 20; i++) {
                let obj = {
                    key: ''
                }
                this.numbers.push()
            }
        },
        components: {
            CityCascader, Patient, PayDialog
        }
    }
</script>
