<template>
    <el-form :model="ruleForm" :rules="rules" label-width="160px">
        <el-form-item label="服务对象姓名：" prop="userName">
            <el-input v-model="ruleForm.name" placeholder="请输入服务对象姓名"></el-input>
        </el-form-item>
        <el-form-item label="服务对象年龄：" prop="userName">
            <el-input v-model="ruleForm.name" placeholder="请输入服务对象年龄"></el-input>
        </el-form-item>
        <el-form-item label="服务对象性别：" prop="userType">
            <el-radio-group v-model="sex">
                <el-radio :label="3">男</el-radio>
                <el-radio :label="6">女</el-radio>
            </el-radio-group>
        </el-form-item>
        <el-form-item label="患病症状：">
            <RichText v-model="ruleForm.introduce" :height="200" />
        </el-form-item>
        <el-form-item label="以往病史：">
            <RichText v-model="ruleForm.introduce" :height="200" />
        </el-form-item>
        <el-form-item label="上传病例：">
            <el-upload
                action="https://jsonplaceholder.typicode.com/posts/"
                list-type="picture-card"
                :limit="5"
                :on-preview="handlePictureCardPreview"
                :on-remove="handleRemove">
                <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible">
                <img width="100%" :src="dialogImageUrl" alt="">
            </el-dialog>
        </el-form-item>
    </el-form>
</template>
<script>
    import RichText from '@/components/RichText/RichText'

    export default {
        data() {
            return {
                ruleForm: {},
                rules: {},
                sex: '',
                dialogImageUrl: '',
                dialogVisible: false
            }
        },
        methods: {
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePictureCardPreview(file) {
                this.dialogImageUrl = file.url;
                this.dialogVisible = true;
            }
        },
        components: {
            RichText
        }
    }
</script>